// import {events} from "../utils/events"
import handlerCollection from "../handler"
import {uniqueId} from "../utils/tools"

function useMenuDrag(data, canvas) {
    let item = null;

    function dragenter(e) {
        e.dataTransfer.dropEffect = "move"
    }

    function dragover(e) {
        e.preventDefault()
    }

    function dragleave(e) {
        e.dataTransfer.dropEffect = "none"
    }

    function drop(e) {
        const currentList = data.value.elementsList;
        data.value = {
            ...data.value,
            elementsList: [...currentList,
                {
                    top: e.offsetY - item.initTop,
                    left: e.offsetX - item.initLeft,
                    key: item.element.key,
                    focus:false,
                    attributes:handlerCollection(item.element.key),
                    id:uniqueId(),
                    flag:true
                }
            ]
        }
    }


    function dragStart(e,element) {
        canvas.value.addEventListener("dragenter", dragenter, false);
        canvas.value.addEventListener("dragover", dragover, false);
        canvas.value.addEventListener("dragleave", dragleave, false);
        canvas.value.addEventListener("drop", drop, false);
        item = {
            element,
            initLeft:e.offsetX,
            initTop:e.offsetY
        }
        // events.emit("start")
    }

    function dragEnd() {
        canvas.value.removeEventListener("dragenter", dragenter);
        canvas.value.removeEventListener("dragover", dragover);
        canvas.value.removeEventListener("dragleave", dragleave);
        canvas.value.removeEventListener("drop", drop);
        item = null
        // events.emit("end")
    }

    return {
        dragStart,
        dragEnd
    }
}

export default useMenuDrag