function ButtonHandler() {
    const data = {
        text: "按钮"
    }

    const attribute = [{
        label: '尺寸',
        key: "input",
        value: "default",
        name: "size"
    }, {
        label: '类型',
        key: "input",
        value: "primary",
        name: "type"
    }, {
        label: '是否为朴素按钮',
        key: "radio",
        value: false,
        name: "plain"
    }, {
        label: '是否为圆角按钮',
        key: "radio",
        value: false,
        name: "round"
    }, {
        label: '是否为加载中状态',
        key: "radio",
        value: false,
        name: "loading"
    }, {
        label: '按钮是否为禁用状态',
        key: "radio",
        value: false,
        name: "disabled"
    }]

    return {
        attribute,
        data
    }
}

export default ButtonHandler