import { defineComponent, inject, computed,onMounted,ref } from "vue";

export default defineComponent({
    props: {
        item: {
            type: Object
        }
    },
    setup(props, ctx) {
        const registrar = inject("registrar"), { key } = props.item;
        const elementStyle = computed(() => {
            return {
                left: `${props.item.left}px`,
                top: `${props.item.top}px`
            }
        })

        const elementAttributes = computed(() => {
            const parameter = {}
            let data = null
            props.item.attributes.attribute.forEach(item => {
                parameter[item.name] = item.value
            })
            data = props.item.attributes.data.text
            return {
                parameter,
                data
            }
        })

        const canvasItem = ref(null)
        onMounted(() => {
            let {offsetWidth,offsetHeight} = canvasItem.value
            props.item.width = offsetWidth
            props.item.height = offsetHeight
        })

        return () => (
           props.item.flag ? (<div class="canvas_item" style={elementStyle.value} ref={canvasItem}>
                {
                    registrar.containerList[key].render(elementAttributes)
                }
            </div> ) :
            null
        )
    }
})