import { defineComponent,ref } from "vue"
import { ElTabs, ElTabPane,ElSwitch,ElInput } from "element-plus";

export default defineComponent({
    props: {
        item: {
            type: Object
        }
    },
    setup(props, ctx) {
        const initCheck = ref("style");

        return () => (
            props.item.value  ?
            (<div class='change_data'>
                <ElTabs v-model={initCheck.value}>
                    <ElTabPane label="样式" name="style">
                        {
                            props.item.value.attributes.attribute.map(item =>(
                                <div class="update">
                                    <span>{item.label}</span>
                                    <span> {item.key==='input' ? <ElInput v-model={item.value} /> : <ElSwitch v-model={item.value}/>}</span>
                                </div>
                            ))
                        }
                    </ElTabPane>
                    <ElTabPane label="数据" name="data">
                            <ElInput v-model={props.item.value.attributes.data.text}/>
                    </ElTabPane>
                </ElTabs>
            </div>) :

            (<div class="noCheckElement">
                <span >未选中组件</span>
            </div>)
        )     
    }
})