import { defineComponent, computed, inject, ref } from 'vue'
import "./editor.scss"
import useMenuDrag from "../hooks/useMenuDrag"
import EditorItem from './editor-item'
import useMark from "../hooks/useMark"
import useElementDrag from '../hooks/useElementDrag'
import useInstruction from "../hooks/useInstruction"
import DataChange from './dataChange'

import remove from "../assets/img/sc.png"

export default defineComponent({
    props: {
        modelValue: {
            type: Object,
            required: true
        }
    },
    emits: ["update:modelValue"],
    setup(props, ctx) {
        const data = computed({
            get() {
                return props.modelValue
            },
            set(newValue) {
                ctx.emit('update:modelValue', newValue)
            }
        })

        const registrar = inject("registrar");
        let canvas = ref(null);

        const { dragStart, dragEnd } = useMenuDrag(data, canvas)
        const { mouseDown, result, clearAllFocus, lastElement } = useMark(data, (e, item) => continueLogic(e, item))
        const { continueLogic, markerLine } = useElementDrag(lastElement, result)
        const { deleteElement } = useInstruction(data,lastElement)


        const commandList = [{
            label:"删除",
            handler:() => deleteElement(),
            src:remove
        },
        // {
        //     label:"重置",
        //     handler:() => commands.redo(),
        //     src:redo
        // }
    ]

  

        return () => (
            <div class="container">

                <div class="left">
                    {
                        registrar.materialsList.map((item, index) => (
                            <div class="left_item">
                                <div>
                                    {item.label}
                                </div>
                                <div
                                    draggable
                                    onDragstart={(e) => dragStart(e, item)}
                                    onDragend={dragEnd}
                                >
                                    {item.preview()}
                                </div>
                            </div>
                        ))
                    }
                </div>

                <div class="center">
                    <div class="title">
                        {
                            commandList.map(item => (
                                <div onClick={item.handler}>
                                    <img src={item.src} />
                                    <div>{item.label}</div>
                                </div>
                            ))
                        }
                    </div>
                    <div class="content">
                        <div class="canvas" ref={canvas} onMousedown={clearAllFocus}>
                            {
                                data.value.elementsList.map((item, index) =>
                                    <EditorItem
                                        class={item.focus ? 'check' : ''}
                                        item={item}
                                        onMousedown={(e) => mouseDown(e, index, item)}
                                    />
                                )
                            }
                            {
                                markerLine.x && <div class="markX" style={{ top: `${markerLine.x}px` }}></div>
                            }
                            {
                                markerLine.y && <div class="markY" style={{ left: `${markerLine.y}px` }}></div>
                            }
                        </div>
                    </div>
                </div>

                <div class="right">
                      <DataChange item={lastElement}/>     
                </div>

            </div>)
    }
})