import {computed,ref} from 'vue'

function useMark(data,callback) {
    let checkedIndex = ref(-1)

    const result = computed(() => {
        const checkedArr = [],unselectedArr = [];
        data.value.elementsList.forEach(item => item.focus ? checkedArr.push(item) : unselectedArr.push(item))
        return {
            checkedArr,
            unselectedArr
        }
    })

    const lastElement = computed(() => data.value.elementsList[checkedIndex.value])

    function clearAllFocus() {
        data.value.elementsList.forEach(item => item.focus = false)
        checkedIndex.value = -1
    }

    function mouseDown(e,index,item) {
        e.stopPropagation()
        if (e.shiftKey) {
            item.focus = !item.focus
        } else {
            if (!item.focus) {
                clearAllFocus()
                item.focus = true
            } else {
                item.focus = false
            }
        }
        checkedIndex.value = index
        callback(e,item)
    }

    return {
        mouseDown,
        result,
        clearAllFocus,
        lastElement
    }
}

export default useMark