function InputHandler() {
    const data = {
        text: ""
    }

    const attribute = [{
        label: '类型',
        key: "input",
        value: "text",
        name: "type"
    }, {
        label: '是否显示切换密码图标',
        key: "radio",
        value: false,
        name: "showPassword"
    }, {
        label: '输入框尺寸',
        key: "input",
        value: "default",
        name: "size"
    }, {
        label: '输入框占位文本',
        key: "input",
        value: "输入框",
        name: "placeholder"
    },{
        label: '禁用状态',
        key: "radio",
        value: false,
        name: "disabled"
    }]

    return {
        attribute,
        data
    }
}

export default InputHandler