import { reactive} from 'vue'
// import {
//     events
// } from '../utils/events'

function useElementDrag(lastElement, result) {
    let dataState = {}
    let markerLine = reactive({
        x:null,
        y:null
    })
    function mousemove(e) {
        let {
            clientX,
            clientY
        } = e;

        // if (!dataState.flag) {
        //     dataState.flag = true
        //     events.emit("start")
        // }

        let newLeft = clientX - dataState.initX,
            newTop = clientY - dataState.initY;


        result.value.checkedArr.forEach(item => {
            item.left += newLeft
            item.top += newTop
        })

        let {
            left:BLeft,
            top:BTop
        } = lastElement.value,
        {
            Length,
            wire
        } = dataState

        for(let i=0 ; i<Length; i++) {
            if (Math.abs(wire.x[i].top - BTop) < 5) {
                markerLine.x = wire.x[i].showTop
                break;
            }
        }

        for(let k=0 ; k<Length; k++) {
            if (Math.abs(wire.y[k].left - BLeft) < 5) {
                markerLine.y = wire.y[k].showLeft
                break;
            }
        }
        dataState.initX = clientX
        dataState.initY = clientY
    }

    function mouseup(e) {
        document.removeEventListener("mousemove", mousemove)
        document.removeEventListener("mouseup", mouseup)
        // if (dataState.flag) {
        //     events.emit("end")
        // }
        dataState = {}
        markerLine.x = null
        markerLine.y = null
       
    }

    function auxiliaryLine() {
        const line = {
            x: [],
            y: []
        }
        let {
            width: BWidth,
            height: BHeight
        } = lastElement.value
        result.value.unselectedArr.forEach(item => {
            if (!item.flag) return
            let {
                top: ATop,
                left: ALeft,
                width: AWidth,
                height: AHeight
            } = item

            line.x.push({
                showTop: ATop,
                top: ATop - BHeight
            })

            line.x.push({
                showTop: ATop,
                top: ATop
            })

            line.x.push({
                showTop: ATop + (AHeight / 2),
                top: (ATop + (AHeight / 2)) - BHeight / 2
            })

            line.x.push({
                showTop: ATop + AHeight,
                top: ATop + AHeight - BHeight
            })

            line.x.push({
                showTop: ATop + AHeight,
                top: ATop + AHeight
            })

            line.y.push({
                showLeft: ALeft + AWidth,
                left: ALeft + AWidth
            })

            line.y.push({
                showLeft: ALeft,
                left: ALeft - BWidth
            })

            line.y.push({
                showLeft: ALeft,
                left: ALeft
            })

            line.y.push({
                showLeft: ALeft + AWidth,
                left: ALeft + AWidth - BWidth
            })

            line.y.push({
                showLeft: ALeft + (AWidth / 2),
                left: ALeft + (AWidth / 2) - BWidth / 2
            })
        })
        
        dataState.Length = line.x.length
        return line
    }

    function continueLogic(e,item) {
        if (!item.focus) return 
        let {
            clientX,
            clientY
        } = e
        dataState.initX = clientX
        dataState.initY = clientY
        // dataState.flag = false
        dataState.wire = auxiliaryLine()
        document.addEventListener("mousemove", mousemove, false)
        document.addEventListener("mouseup", mouseup, false)
    }

    return {
        continueLogic,
        markerLine
    }
}

export default useElementDrag