import { ElButton, ElInput, ElTag } from "element-plus"

function registrar() {
    const materialsList = [],containerList = {};
    return {
        materialsList,
        containerList,
        injection:(element) => {
            materialsList.push(element)
            containerList[element.key] = element
        }
    }
}

const result = registrar()

result.injection({
    label:"按钮",
    preview:() => <ElButton>预览按钮</ElButton>,
    render:(params) => {
     return (
        <ElButton {...params.value.parameter}>{params.value.data}</ElButton>
     )    
    },
    key:"button"
})


result.injection({
    label:"输入框",
    preview:() => <ElInput placeholder="预览输入框" />,
    render:(params) => {
        return (
            <ElInput {...params.value.parameter} v-model={params.value.data} />
        )
    },
    key:"input"
})


result.injection({
    label:"标签",
    preview:() => <ElTag>预览标签</ElTag>,
    render:(params) => {
        return (
            <ElTag {...params.value.parameter}>{params.value.data}</ElTag>
        )
    },
    key:"tag"
})

export default result